/**
 * Main application routes
 */

'use strict';

import errors from './components/errors';
import path from 'path';

export default function(app) {


  app.use(function(req, res, next) {
        if (req.headers.origin) {
            res.header("Access-Control-Allow-Origin", req.headers.origin);
            res.header('Access-Control-Allow-Headers', 'Content-type,Accept,X-Auth-Token,X-Access-Token,X-Key');
        }
        next();
    });


  // Insert routes below
  // Since we removed things folder
app.use('/api/mappings', require('./api/mappings'));
  app.use('/api/defaults', require('./api/defaults'));
  app.use('/api/exports', require('./api/exports'));
  //app.use('/api/things', require('./api/thing'));
  // All undefined asset or api routes should return a 404
  app.route('/:url(api|auth|components|app|bower_components|assets)/*')
   .get(errors[404]);

  // All other routes should redirect to the index.html
  app.route('/*')
    .get((req, res) => {
      res.sendFile(path.resolve(app.get('appPath') + '/index.html'));
    });
}
